.cartItem {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 15px;
    border-bottom: 2px solid gray;
  }
  
  .imageContainer {
    width: 100px;
    height: 100px;
    margin-right: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
  }
  
  .imageContainer img {
    max-width: 100%;
    max-height: 100%;
  }
  
  .addRemoveIcon {
    color: #87979b;
    cursor: pointer;
  }
  
  .spacing {
    padding: 5px;
  }